import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { Compass, Calendar, BookOpen, User } from 'lucide-react'; // Import icons

const BottomNavigation = () => {
  const location = useLocation(); // Get the current location

  // Define the navigation items
  const navItems = [
    { label: 'Explore', icon: <Compass className="w-6 h-6" />, path: '/' },
    { label: 'Events', icon: <Calendar className="w-6 h-6" />, path: '/events' },
    { label: 'Academic', icon: <BookOpen className="w-6 h-6" />, path: '/academic' },
    { label: 'Profile', icon: <User className="w-6 h-6" />, path: '/profile' },
  ];

  return (
    <div className="fixed bottom-0 left-0 right-0 bg-gradient-to-br from-violet-50 via-fuchsia-50 to-cyan-50 border-t border-gray-200 shadow-md">
      <div className="flex justify-around py-4 max-w-md mx-auto">
        {navItems.map((item) => (
          <Link key={item.label} to={item.path} className="flex flex-col items-center">
            <div className={`flex items-center justify-center w-14 h-14 rounded-lg ${location.pathname === item.path ? 'bg-violet-200' : 'bg-transparent'}`}>
              {item.icon}
            </div>
            <span className={`text-xs mt-1 ${location.pathname === item.path ? 'text-violet-600 font-semibold' : 'text-gray-400'}`}>
              {item.label}
            </span>
          </Link>
        ))}
      </div>
    </div>
  );
};

export default BottomNavigation; 